import React from 'react'

class MemeGenerator extends React.Component {
    constructor(){
        super()
        this.state = {
            topText : "",
            bottomText : "",
            randomImg : "http://i.imgflip.com/1bij.jpg",
            allMemeImgs : [],
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }

    componentDidMount(){
        fetch("https://api.imgflip.com/get_memes")
            .then(data => data.json())
            .then(response => {
                const {memes} = response.data
                this.setState({allMemeImgs : memes})
            })
    }

    handleChange() {
        const {value, name} = event.target
        this.setState({[name] : value})
    }

    handleClick() {
        const newUrl = () => {
            return 
        }
    }

    render() {
        return (
            <div>
                <form className="meme-form">
                    {
                        /**
                         * Create 2 input fields, one for the topText and one for the bottomText
                         * Remember that these will be "controlled forms", so make sure to add
                         * all the attributes you'll need for that to work
                         */
                    }    
                    <input 
                        type='text'
                        name='topText'
                        placeholder='Insert top text'
                        value={this.state.topText}
                        onChange={this.handleChange}
                    />
                    <br/>
                    <input 
                        type='text'
                        name='bottomText'
                        placeholder='Insert bottom text'
                        value={this.state.bottomText}
                        onChange={this.handleChange}
                    />
                    <br/>
                </form>
                <button onClick={this.handleClick}>Gen</button>
                <br/>
                <div>
                    <img src={this.state.randomImg} alt="" />
                    <h2 className="top">{this.state.topText}</h2>
                    <h2 className="bottom">{this.state.bottomText}</h2>
                </div>
            </div>
        )
    }
}

export default MemeGenerator